:root {
  --primary: #667eea;
  --secondary: #764ba2;
  --accent: #00f2fe;
  --text: #292d32;
  --glass: rgba(255,255,255,0.08);
  --border: rgba(255,255,255,0.18);
  --radius: 20px;
  --shadow: 0 4px 32px #01061b22;
}

body {
  margin: 0;
  font-family: "Inter", Arial, sans-serif;
  background: #07081a;
  color: var(--text);
}

header, section {
  padding: 48px 0 32px 0;
}
.section-title {
  font-size: 2.3rem;
  font-weight: 700;
  text-align: center;
  background: linear-gradient(90deg, #12c2e9, #c471ed, #f64f59);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 1.5rem;
}
.section-subtitle {
  color: #888;
  margin-bottom: 2rem;
  text-align: center;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}
.glass-card {
  border-radius: var(--radius);
  background: var(--glass);
  border: 1.5px solid var(--border);
  box-shadow: var(--shadow);
  backdrop-filter: blur(14px);
}
.btn {
  padding: 14px 32px;
  border-radius: 16px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1.08rem;
  background: linear-gradient(135deg, #667eea, #f64f59);
  box-shadow: var(--shadow);
  border: none;
  color: white;
  transition: all .2s;
  min-width: 130px;
}
.btn:hover { filter: brightness(1.1); }
input, textarea {
  padding: 10px;
  border-radius: 8px;
  border: 1px solid var(--border);
  background: var(--glass);
  color: var(--text);
  width: 100%;
  font-size: 1em;
  margin-bottom: 1rem;
}
input:focus, textarea:focus { outline: 2px solid var(--primary);}
@media (max-width: 700px) {
  .section-title { font-size: 1.6rem;}
  header, section { padding: 28px 0 16px 0;}
}
